import 'piral/polyfills';
import * as actions from './actions';
import * as React from 'react';
import { renderInstance, PiletApi, useAction } from 'piral';
import { layout, errors } from './layout';

// change to your feed URL here (either using feed.piral.cloud or your own service)
const feedUrl = 'http://localhost:9000/api/v1/pilet';

declare module 'piral-core/lib/types/custom' {
  interface PiralCustomActions {
    dispatchMainVar: void;
  }

  interface PiralCustomState {
    mainVar: number;
  }
}

const { root } = renderInstance({
  layout,
  errors,
  actions,
  state: {
    mainVar: 0,
  },
  requestPilets() {
    return fetch(feedUrl)
      .then(res => res.json())
      .then(res => res.items);
  },
});

setup(root);

function setup(api: PiletApi) {
  const mainVarHandler = useAction('dispatchMainVar');

  React.useEffect((): (() => void) => {
    const handler = el => mainVarHandler;

    api.on('mainVar', handler);

    return () => api.off('mainVar', handler);
  }, []);
}
